body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffd3ea;
  background-image: linear-gradient(315deg, #ffd3ea 0%, #403020 74%);

}

.card {
  background-color: #a38560;
  background-image: linear-gradient(315deg, #a38560 0%, #e0d4ae 74%);
}

.card .btn {
  margin: 0 auto !important;
  color: #000;
  font-weight: 500;
  border-radius: 10px;
  background: #cec4b7 !important;
}

.input-field {
  width: 40% !important;
  display: flex;
  align-items: center;
}

.input-field:focus {
  background-color: transparent;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
  display: flex;
  justify-content: center;
  box-shadow: 0 3px 5px #000000;
}

.btn {
  color: #000;
  font-weight: 500;
  margin-left: 20px;
  border-radius: 15px;
  background: #cec4b7 !important;
}

.nav-wrapper {
  width: 80%;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
}

.card-content {
  flex-grow: 1;
}

nav, footer {
  background-color: #403020 !important;

}

.recipe {
  color: #f7f7f7;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.recipe p {
  font-size: 16px;
}

.recipe img {
  border-radius: 20px;
  margin-top: 20px;
  width: 500px;
  height: auto;
}